@use 'src/styles/variables/z-indexes';

.page-loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--page-background-color);
  z-index: z-indexes.$z-index-page-loader;
  display: flex;
  justify-content: center;
  align-items: center;
}