@use 'src/styles/placeholders/button-placeholder';

.main-button {
  @extend %button;

  &--green {
    @extend %button--green;
  }

  &--red {
    @extend %button--red;
  }

  &--big {
    @extend %button--big;
  }

  &:disabled {
    cursor: default;
    filter: opacity(50%);
  }
}